/* Header */

.header-container {
	background-color: var(--header-bg);
}

.header {
	padding: 40px 0;
	min-height: 695px;

	display: flex;
	justify-content: center;
	align-items: center;

	background-image: url('./../../img/header-bg.png');
	background-repeat: no-repeat;
	background-size: auto;
	background-position: center center;

	color: var(--header-text);
	text-align: center;
}

.header__wrapper {
	padding: 0 15px;
	max-width: 660px;
}

.header__title {
	margin-bottom: 20px;
	font-size: 40px;
	font-weight: 700;
	line-height: 1.4;
}

.header__title strong {
	font-size: 60px;
	font-weight: 700;
}

.header__title em {
	font-style: normal;
	color: var(--accent);
}

.header__text {
	margin-bottom: 40px;
	font-size: 18px;
	line-height: 1.333;
}

.header__text p + p {
	margin-top: 0.5em;
}

/* Socail-media */

.social-media-wrap {
	float: left;
	
	display: flex;
	flex-direction: column;
	row-gap: 50px;
	
	margin-top: 100px;
	width: 35px;	
}
.social-media {
	background-color: transparent;
	width: 35px;
	height: 35px;
	
	font-size: 25px;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 3px;
}
.social-media__icon {
	color: var(--accent);
}
.social-media__icon:hover {
	opacity: 0.8;
}
.social-media:active{
	position: relative;
	top: 1px;
}

@media (max-width: 620px) {
	/* Header  */

	.header {
		min-height: unset;
	}

	.header__title {
		font-size: 30px;
	}

	.header__title strong {
		font-size: 40px;
	}
}

@media (max-width: 400px) {
	/* Header  */

	.header{
		padding: 15px 0;
	}

	.header__title {
		font-size: 35px;
	}

	.header__title strong {
		font-size: 40px;
	}
	/* Social */

	.social-media-wrap{
		row-gap: 20px;
	}
}